import axios from 'axios';
import React, { useEffect, useState } from 'react';
import PercentChange from './PercentChange';
import TableFilters from './TableFilters';
const HeaderInfos = () => {

  const [headerData, setHeaderData] = useState([]);

  useEffect(()=> {
    axios
    .get('https://api.coingecko.com/api/v3/global')
    .then((res) => setHeaderData(res.data.data))
  }, [])


  return (
    <div className="header-container">
      <ul className="title">
        <li>
          <h1>React Crypto Watch</h1>
        </li>
        <li>
          Crypto-monnaie : {headerData.active_cryptocurrencies && headerData.active_cryptocurrencies.toLocaleString()}
        </li>
        <li>
          Market : {headerData.markets && headerData.markets}
        </li>
      </ul>
      <ul className="infos-mkt">
        <li className="global-mkt">
          Global Market Cap :
          <PercentChange percent={headerData.market_cap_change_percentage_24h_usd} />
        </li>
        <li>
          BitCoin Dominance : {headerData.market_cap_percentage && headerData.market_cap_percentage.btc.toFixed(1) + "%"}
        </li>
        <li>
          Ethereum Dominance : {headerData.market_cap_percentage && headerData.market_cap_percentage.eth.toFixed(1) + "%"}
        </li>
      </ul>
      <TableFilters />
    </div>
  );
};

export default HeaderInfos;