import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { setListDisplay } from '../actions/list.action';
import { setStableState } from '../actions/stable.action';

const TableFilters = () => {

  const [showStable, setShowStable] = useState(true);
  const [showFavList, setShowFavlist] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setStableState(showStable));
    dispatch(setListDisplay(showFavList));
  }, [showStable, showFavList])

  return (
    <div className="table-filters">
      <div className="table-filters-container">
        <div className="stable-checkbox-container">
          <input 
            type="checkbox" 
            id="stableCoin" 
            defaultChecked={true}
            onChange={()=> setShowStable(!showStable)} 
          />
          <label htmlFor="stableCoin" >{ showStable ? "Avec Stable Coin" : "Sans Stable Coin" }</label>
        </div>
        <div className="stable-checkbox-container fav-list">
          <input 
            type="checkbox" 
            id="favList" 
            defaultChecked={true}
            onChange={()=> setShowFavlist(!showFavList)} 
          />
          <label htmlFor="favList" >Liste des favoris<img src="./assets/star-full.svg" alt="Fav List" /></label>
        </div>
        {/* <div 
          className={showFavList ? "no-list-btn" : "no-list-btn active"}
          onClick={ ()=>setShowFavlist(false) }
        >
            <p>Aucune Liste</p>
        </div>
        <div 
          className={showFavList ? "fav-list active" : "fav-list"}
          onClick={ ()=>setShowFavlist(true) }
        >
          <p>Liste des favoris</p>
          <img src="./assets/star-full.svg" alt="Fav List" />
        </div> */}
      </div>
    </div>
  );
};

export default TableFilters;